import { createGlobalStyle } from 'styled-components';
import theme from './design-system/theme';
import { axiomTheme, quantumColors } from './axiom-theme';

const GlobalStyles = createGlobalStyle`
  /* Import Quantum Fonts */
  @import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;900&display=swap');
  @import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap');
  @import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@400;500;600&display=swap');

  /* Axiom Loom CSS Custom Properties */
  :root {
    /* Quantum Colors */
    --quantum-deep: ${quantumColors.quantum.deep};
    --quantum-void: ${quantumColors.quantum.void};
    --quantum-dark: ${quantumColors.quantum.dark};
    --quantum-medium: ${quantumColors.quantum.medium};
    --quantum-light: ${quantumColors.quantum.light};
    --quantum-bright: ${quantumColors.quantum.bright};
    --quantum-glow: ${quantumColors.quantum.glow};

    /* Neural Blues */
    --neural-deep: ${quantumColors.neural.deep};
    --neural-dark: ${quantumColors.neural.dark};
    --neural-medium: ${quantumColors.neural.medium};
    --neural-light: ${quantumColors.neural.light};
    --neural-bright: ${quantumColors.neural.bright};
    --neural-glow: ${quantumColors.neural.glow};
    --neural-electric: ${quantumColors.neural.electric};

    /* Plasma Accents */
    --plasma-violet: ${quantumColors.plasma.violet};
    --plasma-cyan: ${quantumColors.plasma.cyan};
    --plasma-magenta: ${quantumColors.plasma.magenta};
    --plasma-gold: ${quantumColors.plasma.gold};
    --plasma-emerald: ${quantumColors.plasma.emerald};
    --plasma-crimson: ${quantumColors.plasma.crimson};

    /* Glass Morphism */
    --glass-background: ${quantumColors.glass.background};
    --glass-border: ${quantumColors.glass.border};
    --glass-hover: ${quantumColors.glass.hover};
    --glass-active: ${quantumColors.glass.active};

    /* Typography */
    --font-primary: ${axiomTheme.typography.fonts.primary};
    --font-mono: ${axiomTheme.typography.fonts.mono};
    --font-display: ${axiomTheme.typography.fonts.display};

    /* Spacing */
    --spacing-px: ${axiomTheme.spacing.px};
    --spacing-1: ${axiomTheme.spacing[1]};
    --spacing-2: ${axiomTheme.spacing[2]};
    --spacing-3: ${axiomTheme.spacing[3]};
    --spacing-4: ${axiomTheme.spacing[4]};
    --spacing-5: ${axiomTheme.spacing[5]};
    --spacing-6: ${axiomTheme.spacing[6]};
    --spacing-8: ${axiomTheme.spacing[8]};
    --spacing-10: ${axiomTheme.spacing[10]};
    --spacing-12: ${axiomTheme.spacing[12]};
    --spacing-16: ${axiomTheme.spacing[16]};
    --spacing-20: ${axiomTheme.spacing[20]};
    --spacing-24: ${axiomTheme.spacing[24]};
    --spacing-32: ${axiomTheme.spacing[32]};
  }

  /* CSS Reset and Base Styles */
  *, *::before, *::after {
    box-sizing: border-box;
  }

  * {
    margin: 0;
    padding: 0;
  }

  html {
    font-size: 16px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    scroll-behavior: smooth;
  }

  body {
    font-family: var(--font-primary);
    font-size: ${props => props.theme.typography.fontSize.base};
    line-height: ${props => props.theme.typography.lineHeight.normal};
    
    /* Dark Theme as Primary */
    color: var(--neural-electric);
    background: linear-gradient(
      135deg,
      var(--quantum-deep) 0%,
      var(--quantum-void) 25%,
      var(--neural-deep) 50%,
      var(--quantum-dark) 75%,
      var(--quantum-void) 100%
    );
    background-attachment: fixed;
    background-size: 400% 400%;
    animation: gradientShift 20s ease infinite;
    
    min-height: 100vh;
    overflow-x: hidden;
  }

  /* Quantum Animation Keyframes */
  @keyframes gradientShift {
    0% { background-position: 0% 50%; }
    50% { background-position: 100% 50%; }
    100% { background-position: 0% 50%; }
  }

  @keyframes neuralPulse {
    0%, 100% {
      opacity: 0.3;
      transform: scale(1);
    }
    50% {
      opacity: 1;
      transform: scale(1.05);
    }
  }

  @keyframes quantumGlow {
    0%, 100% {
      box-shadow: 0 0 20px var(--plasma-violet);
    }
    33% {
      box-shadow: 0 0 30px var(--plasma-cyan);
    }
    66% {
      box-shadow: 0 0 25px var(--plasma-emerald);
    }
  }

  @keyframes holographicShimmer {
    0% { background-position: -200% 0; }
    100% { background-position: 200% 0; }
  }

  @keyframes particleFloat {
    0%, 100% { transform: translateY(0px) translateX(0px); }
    25% { transform: translateY(-10px) translateX(5px); }
    50% { transform: translateY(-5px) translateX(-5px); }
    75% { transform: translateY(-15px) translateX(10px); }
  }

  /* Typography */
  h1, h2, h3, h4, h5, h6 {
    margin-bottom: var(--spacing-4);
    font-family: var(--font-display);
    font-weight: 700;
    line-height: 1.2;
    color: var(--neural-electric);
    
    /* Quantum text glow effect */
    text-shadow: 0 0 10px var(--plasma-cyan);
    
    /* Gradient text for headings */
    background: linear-gradient(
      45deg,
      var(--plasma-violet),
      var(--plasma-cyan),
      var(--plasma-emerald)
    );
    background-size: 200% 200%;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: gradientShift 8s ease-in-out infinite;
  }

  h1 {
    font-size: ${props => props.theme.typography.fontSize['5xl']};
    ${props => props.theme.breakpoints.down.md} {
      font-size: ${props => props.theme.typography.fontSize['4xl']};
    }
  }

  h2 {
    font-size: ${props => props.theme.typography.fontSize['4xl']};
    ${props => props.theme.breakpoints.down.md} {
      font-size: ${props => props.theme.typography.fontSize['3xl']};
    }
  }

  h3 {
    font-size: ${props => props.theme.typography.fontSize['3xl']};
    ${props => props.theme.breakpoints.down.md} {
      font-size: ${props => props.theme.typography.fontSize['2xl']};
    }
  }

  h4 {
    font-size: ${props => props.theme.typography.fontSize['2xl']};
  }

  h5 {
    font-size: ${props => props.theme.typography.fontSize.xl};
  }

  h6 {
    font-size: ${props => props.theme.typography.fontSize.lg};
  }

  p {
    margin-bottom: var(--spacing-4);
    line-height: 1.6;
    color: var(--neural-light);
  }

  /* Links */
  a {
    color: var(--plasma-cyan);
    text-decoration: none;
    position: relative;
    transition: all 0.3s ease;
    
    /* Quantum link glow */
    text-shadow: 0 0 5px var(--plasma-cyan);
    
    &::before {
      content: '';
      position: absolute;
      bottom: -2px;
      left: 0;
      width: 0;
      height: 2px;
      background: linear-gradient(
        90deg,
        var(--plasma-violet),
        var(--plasma-cyan)
      );
      transition: width 0.3s ease;
    }

    &:hover {
      color: var(--plasma-violet);
      text-shadow: 0 0 10px var(--plasma-violet);
      
      &::before {
        width: 100%;
      }
    }

    &:focus {
      outline: none;
      box-shadow: 
        0 0 0 2px var(--plasma-cyan),
        0 0 20px var(--plasma-cyan);
    }
  }

  /* Lists */
  ul, ol {
    margin-bottom: ${props => props.theme.spacing[4]};
    padding-left: ${props => props.theme.spacing[6]};
  }

  li {
    margin-bottom: ${props => props.theme.spacing[2]};
  }

  /* Code */
  code {
    font-family: ${props => props.theme.typography.fontFamily.mono};
    font-size: ${props => props.theme.typography.fontSize.sm};
    background-color: ${props => props.theme.colors.background.secondary};
    padding: ${props => props.theme.spacing[0.5]} ${props => props.theme.spacing[1]};
    border-radius: ${props => props.theme.borderRadius.sm};
  }

  pre {
    font-family: ${props => props.theme.typography.fontFamily.mono};
    font-size: ${props => props.theme.typography.fontSize.sm};
    background-color: ${props => props.theme.colors.background.secondary};
    padding: ${props => props.theme.spacing[4]};
    border-radius: ${props => props.theme.borderRadius.md};
    overflow-x: auto;
    margin-bottom: ${props => props.theme.spacing[4]};
  }

  /* Tables */
  table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: ${props => props.theme.spacing[4]};
  }

  th, td {
    padding: ${props => props.theme.spacing[3]};
    text-align: left;
    border-bottom: 1px solid ${props => props.theme.colors.border.light};
  }

  th {
    font-weight: ${props => props.theme.typography.fontWeight.semibold};
    background-color: ${props => props.theme.colors.background.secondary};
  }

  /* Forms */
  input, textarea, select {
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
  }

  /* Buttons */
  button {
    font-family: inherit;
    cursor: pointer;
  }

  /* Images */
  img {
    max-width: 100%;
    height: auto;
    display: block;
  }

  /* Utility Classes */
  .sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border-width: 0;
  }

  /* Quantum Scrollbar Styling */
  ::-webkit-scrollbar {
    width: 12px;
    height: 12px;
  }

  ::-webkit-scrollbar-track {
    background: var(--glass-background);
    backdrop-filter: blur(0px);
    border-radius: 6px;
  }

  ::-webkit-scrollbar-thumb {
    background: linear-gradient(
      45deg,
      var(--plasma-violet),
      var(--plasma-cyan)
    );
    border-radius: 6px;
    border: 2px solid var(--quantum-void);
    box-shadow: 0 0 10px var(--plasma-violet);
    
    &:hover {
      background: linear-gradient(
        45deg,
        var(--plasma-cyan),
        var(--plasma-emerald)
      );
      box-shadow: 0 0 20px var(--plasma-cyan);
    }
  }

  /* Quantum Selection */
  ::selection {
    background: linear-gradient(
      45deg,
      var(--plasma-violet),
      var(--plasma-cyan)
    );
    color: var(--quantum-deep);
    text-shadow: none;
  }

  /* Quantum Focus Visible */
  :focus-visible {
    outline: none;
    box-shadow: 
      0 0 0 2px var(--plasma-cyan),
      0 0 20px var(--plasma-cyan);
  }

  /* Animations */
  @keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
  }

  @keyframes slideIn {
    from { transform: translateY(20px); opacity: 0; }
    to { transform: translateY(0); opacity: 1; }
  }

  @keyframes scaleIn {
    from { transform: scale(0.95); opacity: 0; }
    to { transform: scale(1); opacity: 1; }
  }

  @keyframes spin {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
  }

  @keyframes pulse {
    0%, 100% { opacity: 1; }
    50% { opacity: 0.5; }
  }

  /* Loading State */
  .loading {
    display: inline-block;
    width: 20px;
    height: 20px;
    border: 2px solid ${props => props.theme.colors.border.light};
    border-top-color: ${props => props.theme.colors.primary.yellow};
    border-radius: 50%;
    animation: spin 0.8s linear infinite;
  }
`;

export default GlobalStyles;